/* eslint-disable react-hooks/exhaustive-deps */
import { useEffect } from "react";
import { useParams, Link, useLocation } from "react-router-dom";
import { ProductE, CategoryFilter } from "../../components";
import style from "../css/ProductS.module.css";
import allProducts from "../../Assets/data/AllProducts.json";

function ProductS(props) {
  const { pathname } = useLocation();

  useEffect(() => {
    props.App.current.scrollTo(0, 0);
    console.log("asas" + props.App.current.screenTop);
  }, [pathname]);

  const url = useParams();
  console.info(url.id);
  return (
    <div className={style.parent}>
      <div className={style.CategoryPanel}>
        <CategoryFilter />
      </div>{" "}
      <ul className={`all_P_ul ${style.products}`}>
        {" "}
        {allProducts.map((item, index) => (
          <li key={index}>
            <ProductE data={item} isnew={index % 3 === 0 ? true : false} />{" "}
          </li>
        ))}{" "}
      </ul>{" "}
    </div>
  );
}

export default ProductS;
