import style from "../css/Dashboard.module.css";
import { ProductE, DashboardElement } from "../../components";
import allProducts from "../../Assets/data/AllProducts.json";
import { Link } from "react-router-dom";

function Dashboard() {
  return (
    <>
      <div className={style.parent}>
        <div className={style.main}>
          <div className={style.h}>
            <h3> my feet brand new shoes </h3>{" "}
          </div>{" "}
          <div className={style.img}>
            <img src={allProducts[0].img} alt="" />
          </div>{" "}
          <div className={style.dimg}>
            <ul>
              <li>
                <img src={allProducts[1].img} alt="" />
              </li>{" "}
              <li>
                <img src={allProducts[5].img} alt="" />
              </li>{" "}
              <li>
                <img src={allProducts[3].img} alt="" />
              </li>{" "}
            </ul>{" "}
          </div>{" "}
          <div className={style.t}>
            <p>
              Lorem ipsum dolor sit amet, consectetur adipiscing elit.Arcu felis
              convallis fringilla lacus nulla bibendum duis nulla a.Sociis nec,
              quis vitae imperdiet.Iaculis massa nulla elit id elementum sapien
              sem scelerisque suscipit.Nisi, tincidunt ac eget nisi, massa odio
              morbi.{" "}
            </p>{" "}
          </div>{" "}
          <div className={style.btn}>
            <input type="button" value="View more" />
          </div>{" "}
          <div className={style.v}>
            <ul>
              <li className={style.Vdont}> </li>{" "}
              <li className={style.Vdont}> </li>{" "}
              <li className={style.Vdont}> </li>{" "}
            </ul>{" "}
          </div>{" "}
        </div>{" "}
        <div className={style.left}>
          <DashboardElement img={allProducts[13].img} />{" "}
        </div>{" "}
        <div className={style.right}>
          <DashboardElement img={allProducts[3].img} />{" "}
        </div>{" "}
        <div className={style.right_btm}>
          <DashboardElement img={allProducts[0].img} />{" "}
        </div>{" "}
      </div>{" "}
      <div className={style.allProducts}>
        <ul className="all_P_ul">
          {" "}
          {allProducts.map((item, index) => (
            <li key={index}>
              <ProductE data={item} isnew={index % 3 === 0 ? true : false} />{" "}
            </li>
          ))}{" "}
        </ul>{" "}
        <div className={style.shop}>
          <div className={style.line}> </div>{" "}
          <div className={style.buton}>
            <Link to="/Products/all">
              <input type="button" value="SHOP NOW" />
            </Link>{" "}
          </div>{" "}
          <div className={style.line}> </div>{" "}
        </div>{" "}
      </div>{" "}
    </>
  );
}

export default Dashboard;
