/* eslint-disable react/jsx-pascal-case */
import style from "../css/productE.module.css";
import * as Icon_md from "react-icons/md";
import { Link } from "react-router-dom";
function ProductE(props) {
  return (
    <div className={style.parent}>
      {" "}
      <div className={style.img}>
        <img src={props.data.img} alt="Product img" />
        <div className={style.hover_details}>
          <div className={style.add_cart}>
            <div className={style.cart}>
              <p> Add Cart </p>{" "}
            </div>{" "}
          </div>{" "}
          <div className={style.fav}>
            <div className={style.icon}>
              <Icon_md.MdFavoriteBorder className={style.c_icon} />{" "}
            </div>{" "}
          </div>{" "}
          <div className={style.state}>
            <div
              className={
                props.isnew
                  ? `${style.s} ${style.sell}`
                  : `${style.s} ${style.new}`
              }
            >
              {props.isnew ? <p> Sell </p> : <p>New</p>}{" "}
            </div>{" "}
          </div>{" "}
        </div>{" "}
      </div>{" "}
      <div className={style.data}>
        <div className={style.name_conatiner}>
          <p className={style.name}> {props.data.name} </p>{" "}
          <div className={style.toltips}> {props.data.name} </div>{" "}
        </div>{" "}
        <div className={style.price}>
          <h4> {props.data.price}$</h4>{" "}
        </div>{" "}
        <div className={style.category}>
          <Link to={`/Products/${props.data.category}`}>
            <p> {props.data.category} </p>{" "}
          </Link>{" "}
        </div>{" "}
      </div>{" "}
    </div>
  );
}

export default ProductE;
